.text-left {
  text-align: left;
}

.game {
  margin-top: 130px;
  min-height: 1250px;
  background-image: url("../../assets/images/fondo.png");
  background-repeat: repeat;
  background-size: 400px;
  background-color: white;
  padding-bottom: 100px;
}
.game img {
  width: 450px;
}
.game .game-img {
  border-image: url("../../assets/images/marco1.png") 80 80 80 80 stretch stretch;
  border-style: inset;
  border-width: 50px;
}
.game .container {
  padding-top: 30px;
  color: #221f43;
}
.game .container p, .game .container strong {
  color: #221f43;
}
.game .container iframe {
  border-image: url("../../assets/images/marco_video.png") 80 80 80 80 stretch stretch;
  border-style: inset;
  border-width: 34px;
  width: 90%;
  height: 350px;
}
.game .container .description {
  text-align: justify;
}
.game .difficulty {
  padding: 0;
  margin: auto;
}
.game .difficulty p {
  text-align: end;
  text-transform: uppercase;
  position: relative;
  top: 15px;
}
.game .difficulty .column {
  padding: 0;
}
.game .difficulty-img {
  height: 50px;
  width: auto;
  text-align: end;
}
.game .transparent {
  background: transparent;
}
.game .print-img-button {
  margin-top: 100px;
  width: 250px;
  margin-bottom: 50px;
  cursor: pointer;
  transition: all 0.4s linear;
}
.game .print-img-button:hover {
  opacity: 0.8;
}
.game .teacher-button {
  width: 360px;
  margin-bottom: 100px;
  display: block;
  margin-top: 110px;
  cursor: pointer;
  transition: all 0.4s linear;
}
.game .teacher-button:hover {
  opacity: 0.8;
}
.game .banner {
  padding-bottom: 30px;
}
.game .banner img {
  width: 300px;
}
.game .banner .text-list {
  margin: auto;
}
.game .banner .column {
  margin: auto;
}
.game .breadcrumb .navbar-item {
  min-width: 200px;
}
.game .breadcrumb a {
  width: 100%;
  display: revert;
  color: #221f43;
}
.game .breadcrumb a .text {
  padding-left: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.game .breadcrumb li::before {
  margin-right: 10px;
}
.game p.title {
  color: #5f45bf;
  font-size: 30px;
}
.game p.how {
  text-transform: uppercase;
  color: #5f45bf;
  font-size: 18px;
}
.game .content p {
  text-align: left;
}
.game .content strong {
  text-transform: uppercase;
  color: #221f43;
  font-weight: 300;
}

@media print {
  .no-print {
    display: none !important;
  }
}
.games-grid {
  margin-top: 50px;
}
.games-grid .game-detail .bg-img {
  background-image: url("../images/juego.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  height: 300px;
  width: 80%;
  margin: auto;
  transition: all 0.2s linear;
  cursor: pointer;
}
.games-grid .game-detail .bg-img:hover {
  opacity: 0.8;
}
.games-grid .game-detail .bg-img:hover a {
  opacity: 0.9;
  background: #657f8f;
  color: #ffffff;
}
.games-grid .game-detail a {
  position: relative;
  top: 230px;
  background: white;
  width: 100%;
  display: block;
  opacity: 0.85;
  height: 70px;
  padding: 22px;
  transition: all 0.2s linear;
}
.games-grid .game-detail .card {
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.35);
  margin: 10px;
  margin-bottom: 0;
  padding: 0px;
  height: 310px;
  opacity: 1;
  transition: all 0.6s ease;
  border-radius: 5px;
  cursor: pointer;
}
.games-grid .game-detail .card:hover {
  opacity: 0.6;
}
.games-grid .game-detail .card .card-header {
  border-radius: 5px;
  height: 60px;
  padding: 10px;
  background-color: #332d6b;
  height: 20%;
}
.games-grid .game-detail .card .card-image {
  height: 80%;
}
.games-grid .game-detail .card .card-image .image {
  height: 100%;
}
.games-grid .game-detail .card .card-image img {
  max-height: 100%;
}
.games-grid .game-detail .card p {
  color: white;
  text-transform: uppercase;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  letter-spacing: 3px;
  font-weight: lighter;
  font-size: 20px;
}
.games-grid .t {
  width: 100%;
  margin-top: 30px;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bolder;
}

.capitalized {
  text-transform: capitalize;
}

.game-pdf-btn {
  padding: 20px;
  margin: 40px;
  text-transform: uppercase;
  font-weight: 800;
  border-radius: 11px;
}