.text-left {
  text-align: left;
}

.games {
  margin-top: 130px;
  min-height: 100vh;
  background-image: url("../../assets/images/fondo.png");
  background-repeat: repeat;
  background-size: 400px;
  background-color: #332d6b;
  padding-bottom: 100px;
}
.games .breadcrumb {
  padding: 10px 80px;
}
.games .breadcrumb a {
  color: white;
  padding: 0 30px;
}
.games .breadcrumb a .text {
  padding-left: 10px;
}
.games .breadcrumb li::before {
  margin-right: 10px;
}
.games .breadcrumb .is-active {
  color: white;
  padding-left: 30px;
}
.games .is-fullhd {
  padding: 0 20px;
}
.games .gamelist {
  max-width: 100%;
  margin-top: 30px;
}
.games .results-container {
  width: 60%;
  padding: 12px;
  display: block;
  margin: auto;
  position: relative;
  top: 95px;
}
.games .results-container .bg-img {
  border-right: 10px solid #657f8f;
  background-image: url("../../assets/images/sample_juego.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.games .results-container ul {
  text-align: left;
  list-style-type: disc;
  margin-left: 30px;
}
.games .dropdown {
  width: 100%;
}
.games ::v-deep .autocomplete {
  width: 70%;
  margin: auto;
}
.games ::v-deep .dropdown-trigger {
  width: 100%;
}
.games ::v-deep .dropdown-trigger button {
  width: 100%;
  background-color: rgba(101, 127, 143, 0.7);
}
.games ::v-deep .input {
  background-color: rgba(101, 127, 143, 0.7);
  border-color: transparent;
}
.games ::v-deep .input::placeholder {
  color: #ffffff;
}
.games .search-btn {
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
}
.games .menu {
  height: 100vh;
  padding-top: 40px;
  background: rgba(254, 254, 255, 0.6196078431);
  color: #0f0d2a;
}
.games .menu .menu-list {
  line-height: 2.25;
  padding-left: 70px;
}
.games .menu .menu-list .menu-label {
  text-align: left;
  color: #0f0d2a;
}
.games .menu .toggle-all {
  margin-top: -20px;
  letter-spacing: 1px;
  text-transform: none;
  color: #0f0d2a;
  text-align: left;
}
.games .menu .toggle-all small {
  cursor: pointer;
  text-decoration: underline;
}
.games .menu ::v-deep .checkbox {
  width: 100%;
  text-align: left;
}
.games .menu .b-checkbox.checkbox + .checkbox {
  margin-left: 0;
}
.games .games-grid .game-detail .bg-img {
  background-image: url("../images/juego.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  height: 300px;
  width: 80%;
  margin: auto;
  transition: all 0.2s linear;
  cursor: pointer;
}
.games .games-grid .game-detail .bg-img:hover {
  opacity: 0.8;
}
.games .games-grid .game-detail .bg-img:hover a {
  opacity: 0.9;
  background: #657f8f;
  color: #ffffff;
}
.games .games-grid .game-detail a {
  position: relative;
  top: 230px;
  background: white;
  width: 100%;
  display: block;
  opacity: 0.85;
  height: 70px;
  padding: 22px;
  transition: all 0.2s linear;
}
.games .games-grid .game-detail .card {
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.35);
  background-color: white;
  margin: 10px;
  margin-bottom: 0;
  padding: 0px;
  height: 310px;
  opacity: 1;
  transition: all 0.6s ease;
  border-radius: 5px;
  cursor: pointer;
}
.games .games-grid .game-detail .card:hover {
  opacity: 0.6;
}
.games .games-grid .game-detail .card .card-header {
  border-radius: 5px;
  height: 60px;
  padding: 10px;
  background-color: #100d2b;
  height: 20%;
}
.games .games-grid .game-detail .card .card-image {
  height: 80%;
}
.games .games-grid .game-detail .card .card-image .image {
  height: 100%;
}
.games .games-grid .game-detail .card .card-image img {
  max-height: 100%;
}
.games .games-grid .game-detail .card p {
  color: white;
  text-transform: uppercase;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  letter-spacing: 3px;
  font-weight: lighter;
  font-size: 20px;
  line-height: 40px;
}
.games .games-grid .t {
  width: 100%;
  font-size: 13px;
  color: white;
}
.games ::v-deep .modal-content {
  width: 100%;
  background: #0f0d2a;
  color: white;
  padding: 20px;
}
.games ::v-deep .modal-content strong {
  color: white;
}
.games ::v-deep .modal-content .paralem {
  display: inline-block;
  width: 110px;
  height: 30px;
  margin: 10px;
  border-left: 6px solid;
  -moz-transform: skew(-20deg);
  -o-transform: skew(-20deg);
  -ms-transform: skew(-20deg);
  -webkit-transform: skew(-20deg);
  transform: skew(-20deg);
}
.games ::v-deep .modal-content .paralem p {
  position: relative;
  top: 2px;
  color: #ffffff;
  font-weight: bolder;
  -moz-transform: skew(20deg);
  -o-transform: skew(20deg);
  -ms-transform: skew(20deg);
  -webkit-transform: skew(20deg);
  transform: skew(20deg);
}
.games ::v-deep .modal-content img {
  max-width: 320px;
  display: block;
  margin: 30px auto;
}
.games ::v-deep .modal-content .title {
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 20px;
  font-size: 30px;
}
.games ::v-deep .modal-content .description {
  padding: 30px;
}

.filter-container {
  position: fixed;
  bottom: 20px;
  left: 30px;
  height: 100px;
  width: 100px;
  background-color: #302b63;
  border-radius: 100%;
  display: block;
}
.filter-container button {
  display: block;
  margin: auto;
  position: relative;
  top: 25px;
  right: 10px;
  left: 2px;
  font-size: 50px;
  background: transparent;
  border: 0;
  color: white;
  text-align: center;
  outline: 0;
  cursor: pointer;
  opacity: 1;
}
.filter-container button:hover {
  opacity: 0.7;
}

.card-image img {
  max-width: 320px;
  display: block;
  margin: auto;
}

iframe {
  width: 100%;
  height: 300px;
}

.lds-dual-ring {
  margin-top: 150px;
  display: inline-block;
  width: 80px;
  height: 80px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fcf;
  border-color: #fcf transparent #fcf transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}