@import url("https://fonts.googleapis.com/css?family=Righteous");
body {
  background-color: #1f1c43;
}

@media (max-width: 414px) {
  html {
    min-width: 103vw;
  }
}

#app {
  font-family: "Righteous", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
}

html, body, pre, code, kbd, samp, button, a, input {
  font-family: "Righteous", cursive;
}

.school-dd {
  overflow-y: auto;
  width: 280px;
  display: table-row;
}

.dd-tile ::v-deep .dropdown-menu {
  height: 300px;
  overflow-y: auto;
  width: 280px;
}

.navbar {
  background-color: white;
  height: 130px;
}
.navbar ::v-deep .navbar-burger {
  color: white;
  position: relative;
  top: 30px;
  left: 10px;
}
.navbar.nav-fixed {
  position: fixed;
  width: 100%;
  top: 0px;
}

.footer {
  width: 100%;
  background-color: white;
  color: #0f0d2a;
  padding: 40px 20px;
  font-size: 12px;
  margin-top: 30px;
}
.footer p, .footer strong {
  color: #0f0d2a;
  text-transform: uppercase;
  font-family: "Righteous", cursive;
}

.navbar-brand {
  height: 100px;
  width: 25%;
  position: relative;
  top: 10px;
}
@media (max-width: 1096px) {
  .navbar-brand {
    width: 90%;
  }
}
.navbar-brand .navbar-item {
  padding-left: 10%;
  max-width: 95%;
}
@media (max-width: 1096px) {
  .navbar-brand .navbar-item {
    max-height: 90%;
  }
}
.navbar-brand img {
  max-width: 230px;
  max-height: 90%;
  display: block;
  margin: auto;
  box-sizing: unset;
  position: relative;
  left: 0;
}

.navbar-menu {
  background-color: white;
}
.navbar-menu .navbar-item {
  font-size: 19px;
  padding: 25px 15px;
  text-transform: uppercase;
  color: gray;
}
.navbar-menu .navbar-item i {
  color: #00d8cd;
  opacity: 0.6;
}
.navbar-menu .navbar-item.router-link-active {
  color: #0f0d2a;
  background: inherit;
}
.navbar-menu .navbar-item.router-link-active i {
  color: #00d8cd;
}
.navbar-menu .navbar-item:hover {
  background-color: white !important;
}
.navbar-menu .navbar-item .ditem {
  text-transform: capitalize !important;
}
.navbar-menu .close-btn {
  background-color: transparent;
  color: #0f0d2a;
  display: inline-block;
  padding: 0px;
}
.navbar-menu .close-btn span {
  font-size: 11px;
  position: relative;
  top: 6px;
}

.user-name {
  font-size: 12px;
  position: relative;
  right: 40%;
  top: 20px;
}
.user-name .user-text {
  text-overflow: ellipsis;
  overflow: hidden;
  width: 150px;
  display: inline-block;
  white-space: nowrap;
  position: relative;
  top: 10px;
}
.user-name .icon {
  position: relative;
  left: 20px;
  top: 5px;
}

#landing-navbar .navbar-start .navbar-item {
  display: block;
}
#landing-navbar .navbar-start .navbar-item span {
  position: relative;
  top: 25px;
}
@media (max-width: 414px) {
  #landing-navbar .navbar-start .navbar-item span {
    top: 0px;
  }
}
#landing-navbar .tile.no-m {
  margin-bottom: 5px !important;
}
#landing-navbar .buttons .close-btn {
  display: block;
  position: relative;
  bottom: 10px;
  width: 100%;
  text-align: right;
}
#landing-navbar .user-name {
  font-size: 14px;
  display: block;
  width: 100%;
  left: 0;
  top: 0px;
}